/*---
title: Accordion
section: Objects
---*/

:root {
  --accordion-top-color: var(--blue-100);
  --accordion-mid-color: var(--blue-50);
  --accordion-bottom-color: var(--blue-50);
  --accordion-hover-color: var(--blue-800);
  --accordion-text-color: var(--grey-800);
  --accordion-toggle-color: var(--grey-500);
}

.o-accordion {
  @mixin reset-box;
  color: var(--accordion-text-color);
  position: relative;
}

.o-accordion__list { @mixin reset-box; }

.o-accordion__list--top {
  overflow-x: hidden;
  overflow-y: auto;
}

.o-accordion__label {
  background-color: var(--accordion-top-color);
  color: var(--accordion-text-color);
  display: block;
  font-weight: bold;
  margin-bottom: 0 !important;
  padding: .5rem 1.5rem;
  position: relative;
  text-transform: uppercase;
  transition: background-color var(--global-duration) ease-out, color var(--global-duration) ease-out;
  user-select: none;

  &:hover,
  &:focus { text-decoration: none; }

  &::before {
    @mixin triangle right, var(--grey-700), 5px;
    cursor: pointer;
    left: 10px;
    position: absolute;
    top: 13px;
    transition: transform var(--global-duration) ease-out, border var(--global-duration) ease-out;
  }
}

.o-accordion__item {
  cursor: pointer;
  position: relative;

  &.is-open {
    & > .o-accordion__list {
      @mixin fade-in;
      display: inherit;
    }

    & .o-accordion__label::before { transform: rotate(90deg); }
  }
}

.o-accordion__list--mid {
  @mixin fade-out;
  display: none;
  background-color: var(--accordion-mid-color);

  & .o-accordion__item {
    min-height: 50px;
    padding: .5em 1rem;

    &:not(:last-child) { border-bottom: 1px solid var(--grey-300); }

    &.is-active,
    &:hover,
    &:focus {
      background-color: var(--blue-100);
      box-shadow: inset 0 0 4px rgba(0, 0, 0, .5);

      & .o-accordion__action { @mixin fade-in; }
    }
  }
}

.o-accordion__list--bottom {
  @mixin fade-out;
  background-color: var(--accordion-bottom-color);
}

.o-accordion__title {
  @mixin ellipsis;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.5;
  margin: 0;
  text-transform: uppercase;
  width: 14em;
}

.o-accordion__action {
  @mixin fade-out;
  background-color: var(--green-700);
  color: contrast(var(--green-700));
  cursor: pointer;
  font-size: 20px;
  height: 100%;
  overflow: hidden;
  position: absolute;
  right: 0;
  top: 1px;
  width: 50px;

  &:hover,
  &:focus { background-color: var(--green-600); }
}

.o-accordion__toggle {
  background-color: var(--accordion-toggle-color);
  display: block;
  height: 24px;
  position: relative;

  &::after {
    position: absolute;
    top: 7px;
  }

  .o-accordion--left {
    border-radius: 0 0 var(--global-rounded) 0;

    &::after {
      @mixin triangle left, var(--white), 5px;
      right: 10px;
    }
  }
  
  .o-accordion--right {
    border-radius: 0 0 0 var(--global-rounded);

    &::after {
      @mixin triangle right, var(--white), 5px;
      left: 10px;
    }
  }
}
