/*---
title: Validation
section: Components
---*/

.c-validation {
  background-color: var(--blue-600);
  border-radius: 0 0 var(--global-rounded) var(--global-rounded);
  color: var(--white);
  font-size: 12px;
  line-height: 1.3;
  margin-bottom: 16px;
  margin-top: -18px;
  padding: 5px 7px 3px;
  position: relative;
  transition-duration: var(--global-duration);
  user-select: none;
  z-index: -1;
}

.c-validation--warning {
  background-color: var(--gold-50);
  color: var(--body-copy);
}

.c-validation--error { background-color: var(--red-600); }
