/*---
title: Size
import: size.md
section: Elements
---*/

/* stylelint-disable rule-properties-order, function-whitespace-after */

:root {
  --global-duration: .1s;
  --global-font-size: 16px;
  --global-margin: 8px;
  --global-rounded: 4px;
  --global-line-height: 32px;
  --global-z-index: 100;
  --frame-quarter: calc(var(--global-line-height) / 4);
  --frame-third: calc(var(--global-line-height) / 3);
  --frame-half: calc(var(--global-line-height) / 2);
  --frame: var(--global-line-height);
  --frame-double: calc(var(--global-line-height) * 2);
  --frame-triple: calc(var(--global-line-height) * 3);
}

@custom-selector :--container-sm [class*='column--1'], [class*='column--2'], [class*='column--3'], [class*='column--4'];
@custom-selector :--container-md [class*='column--5'], [class*='column--6'], [class*='column--7'], [class*='column--8'];
@custom-selector :--container-lg [class*='column--9'], [class*='column--10'], [class*='column--11'], [class*='column--12'];

@define-mixin leader $lines: 1, $property: margin-top {
  $(property): calc(var(--global-line-height) * $lines) !important;
}

@define-mixin trailer $lines: 1 {
  @mixin leader $lines, margin-bottom;
}

/* http://blog.teamtreehouse.com/css-tip-better-rounded-borders */
@define-mixin curves
  $curves-border-width: 4px,
  $curves-radius-size: 1.9 {
  border-radius: calc($curves-border-width * $curves-radius-size);
  border-width: $curves-border-width;
}

/* stylelint-enable */

.u-leader--none { margin-top: 0 !important; }

.u-leader--tenth { @mixin leader .1; }
.u-leader--eighth { @mixin leader .125; }
.u-leader--sixth { @mixin leader .1666; }
.u-leader--fifth { @mixin leader .2; }
.u-leader--quarter { @mixin leader .25; }
.u-leader--third { @mixin leader .3; }
.u-leader--half { @mixin leader .5; }
.u-leader { @mixin leader; }
.u-leader--double { @mixin leader 2; }
.u-leader--triple { @mixin leader 3; }

.u-trailer--tenth { @mixin trailer .1; }
.u-trailer--eighth { @mixin trailer .125; }
.u-trailer--sixth { @mixin trailer .1666; }
.u-trailer--fifth { @mixin trailer .2; }
.u-trailer--quarter { @mixin trailer .25; }
.u-trailer--third { @mixin trailer .3; }
.u-trailer--half { @mixin trailer .5; }
.u-trailer { @mixin trailer; }
.u-trailer--double { @mixin trailer 2; }
.u-trailer--triple { @mixin trailer 3; }

.u-width--full { width: 100%; }