/*---
title: Colour
import: colour.md
section: Elements
---*/

/* stylelint-disable rule-properties-order */

:root {
  --grey-50: #fcfcf9;
  --grey-100: #eeeee7;
  --grey-300: #ddddd4;
  --grey-500: #ccccc2;
  --grey-600: #a3a396;
  --grey-700: #77776d;
  --grey-800: #33332a;
  --black: #000;
  --white: #fff;

  --blue-50: #f0faff;
  --blue-100: #cef;
  --blue-200: #aaddfa;
  --blue-300: #94d8ff;
  --blue-400: #85cce5;
  --blue-500: #55bee5;
  --blue-600: #00a0d0;
  --blue-700: #1c7dab;
  --blue-800: #047;
  --blue-900: #0d2c54;
  --blue-1000: #024;
  --blue-1100: #002;

  --orange-500: #f92;
  --orange-600: #f07700;
  --orange-700: #cc5000;

  --red-50: #fbb;
  --red-600: #d03000;
  --red-700: #900;
  --red-800: #800;

  --gold-50: #feb;
  --gold-200: #ffd666;
  --gold-300: #ffc838;
  --gold-500: #ffba0a;
  --gold-700: #eeb111;

  --green-50: #b6f5e3;
  --green-600: #09b081;
  --green-700: #00996e;
  --green-800: #007a56;

  --purple-a100: #831dcb;
  --magenta-500: #d95db7;
}

/* stylelint-enable */

/* Type */

:root {
  --body-copy: var(--grey-800);
  --placeholder-copy: var(--grey-500);
  --positive-copy: var(--green-700);
  --informative-copy: var(--blue-700);
  --neutral-copy: var(--grey-700);
  --live-copy: var(--gold-700);
  --alterable-copy: var(--orange-700);
  --negative-copy: var(--red-600);
}

.is-positive { color: var(--positive-copy); }
.is-informative { color: var(--informative-copy); }
.is-neutral { color: var(--neutral-copy); }
.is-live { color: var(--live-copy); }
.is-alterable { color: var(--alterable-copy); }
.is-negative { color: var(--negative-copy); }

/* Navigation Colours */

:root {
  --bar-color: var(--primary-color);
  --bar-border-color: var(--blue-700);
  --nav-color: var(--primary-color);
  --nav-copy: var(--grey-500);
  --nav-active-color: var(--blue-700);
  --nav-active-copy: var(--white);
  --link-color: var(--blue-700);
  --sidenav-color: var(--blue-50);
  --sidenav-shadow: 0 0 6px rgba(0, 0, 0, .3);
}

/* Backgrounds and Borders */

:root {
  --background-color: var(--grey-50);
  --shadow-color: rgba(51, 51, 42, .6);
  --primary-color: var(--blue-900);
  --secondary-color: var(--orange-600);
  --border-color: var(--grey-300);
  --body-border-color: var(--border-color);

  --callout-bg-color: var(--white);
  --toggle-border-color: var(--grey-600);
  --iconbar-border-color: var(--border-color);
}

/* Highlight */

:root {
  --highlight-announcement: var(--blue-50);
  --highlight-error: var(--red-50);
  --highlight-success: var(--green-50);
  --highlight-warning: var(--gold-50);
}

/* Survey Themes */

:root {
  --theme-purple-text: #735d85;
  --theme-purple-fill: var(--purple-a100);
}

/* Social Media */

:root {
  --facebook: #4c66a4;
  --google-plus: #dd4b39;
  --linkedin: #0077b5;
  --pinterest: #cb2027;
  --stumbleupon: #ff4522;
  --tripadvisor: #629d2a;
  --trustyou: #3498db;
  --tumblr: #35465c;
  --twitter: #2fc2ef;
}
