/*---
section: Components
title: Buttons
import: button.md
---*/

:root {
  --button-background: var(--blue-800);
  --button-background-hover: var(--blue-700);
  --button-color: contrast(var(--button-background));
  --button-disabled-opacity: .35;
  --button-font-size: 14px;
  --button-font-size-lg: 18px;
  --button-round: var(--global-rounded);
}

@define-mixin button-base
  $button-display: inline-block {
  appearance: button;
  border-width: 0;
  cursor: pointer;
  display: $button-display;
  height: 30px;
  line-height: 30px;
  max-width: 100%;
  min-width: 80px;
  outline: 0;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
}

@define-mixin button-solid
  $button-color: #333,
  $button-hover-color: #555,
  $button-text-color: #fff,
  $button-hover-shadow: none,
  $button-radius: 4px,
  $button-duration: .1s,
  $button-disabled-opacity: .35 {
  background-color: $button-color;
  border-radius: $button-radius;
  color: $button-text-color;
  transition: background $button-duration ease, color $button-duration ease;

  &::after {
    border-radius: inherit;
    box-shadow: $button-hover-shadow;
    bottom: 0;
    content: '';
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity $button-duration ease;
    z-index: 5;
  }

  &:focus { outline: 0; }

  &:hover {
    &::after { opacity: 1; }
  }

  @mixin active {
    background-color: $button-hover-color;

    &::after { opacity: 0 !important; }
  }

  @mixin disabled {
    opacity: $button-disabled-opacity;
    -ms-pointer-events: none;
    pointer-events: none;
  }
}

.c-button--wide {
  @mixin button-base block;
  margin: 0 16px 16px 0;
  padding: 12.8px 24px;
  width: 100%;
}

.c-button {
  @mixin button-base inline-block;
  @mixin button-solid var(--button-background), var(--button-background-hover), #fff, 0 0 6px var(--shadow-color), var(--button-round);
  font-family: inherit;
  font-size: var(--button-font-size);
  margin: 0 16px 16px 0;
  overflow: visible; /* Internet Explorer 12- */
  padding: 0 8px;

  &::-moz-focus-inner {
    border: 0 !important;
    padding: 0 !important;
  }

  &:last-child { margin-right: 0 !important; }
  
  &__content { display: inline-block; }

  .c-icon {
    height: 20px;
    margin-top: -4px;
    width: 20px;
  }

  .c-icon:first-child { margin-right: 7px; }

  .c-icon:last-child { margin-left: 7px; }
}

.c-button--round { border-radius: 50px !important; }

.c-button--large {
  font-size: var(--button-font-size-lg) !important;
  height: 36px;
  line-height: 36px;
  min-width: 100px;
}

.c-button--icon {
  background-color: transparent;
  color: var(--grey-700);
  display: inline-block;
  height: 24px;
  line-height: 24px;
  min-width: auto;

  &:focus { outline: 0; }

  &:hover { color: var(--grey-800); }

  @mixin active {
    background-color: transparent;
    color: var(--button-background-hover);
  }

  &::after { opacity: 0 !important; }

  .c-icon {
    height: inherit;
    margin: 0 !important;
    width: inherit;
  }
}

.c-button--postfix {
  border-radius: 0 var(--global-rounded) var(--global-rounded) 0;
  margin-left: -4px;
}

.c-button--postinset {
  background-color: transparent;
  margin: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.c-button--success {
  @mixin button-solid var(--green-700), var(--green-600), var(--white), 0 0 6px var(--shadow-color), var(--button-round);
}

.c-button--secondary {
  @mixin button-solid var(--orange-700), var(--orange-600), var(--white), 0 0 6px var(--shadow-color), var(--button-round);
}

.c-button--alert {
  @mixin button-solid var(--red-700), var(--red-600), var(--white), 0 0 6px var(--shadow-color), var(--button-round);
}

.c-button--passive {
  @mixin button-solid var(--white), var(--blue-200), var(--grey-800), 0 0 6px var(--shadow-color), var(--button-round);
  border: 1px solid var(--grey-600);
}

.c-button--text {
  @mixin button-solid var(--background-color), var(--grey-100), var(--grey-800), 0 0 6px var(--shadow-color), var(--button-round);
  border: 1px solid transparent;

  @mixin active {
    border: 1px solid var(--grey-600);
  }
}

.c-button--anchor {
  @mixin button-solid transparent, transparent, var(--blue-700), none, var(--button-round);
  text-transform: uppercase;

  @mixin active {
    color: var(--blue-800);
    text-decoration: underline !important;
  }
}

