/*---
title: Positioning
section: Utilities
---*/

@define-mixin sticky-footer $height {
  height: auto !important;
  height: 100%;
  margin-bottom: -$height;
  min-height: 100%;

  &__container { padding: 0 0 $height 0; }

  &__footer { height: $height; }
}

@define-mixin plank $type: fixed {
  left: 0;
  position: $type;
  right: 0;
  width: 100%;
}

@define-mixin position $attachment, $type: fixed, $distance:0 {
  @if $attachment == bottom { bottom: $distance; }
  @if $attachment == bottom-left { bottom: $distance; left: $distance; }
  @if $attachment == bottom-right { bottom: $distance; right: $distance; }
  @if $attachment == middle {
    bottom: auto;
    left: 50%;
    right: auto;
    top: 50%;
    transform: translate(-50%, -50%);

    /* Bug with font-smoothing vs translate */

    *,
    *::before,
    *::after {
      transition: none !important;
    }
  }
  @if $attachment == top { top: $distance; }
  @if $attachment == top-left { top: $distance; left: $distance; }
  @if $attachment == top-right { top: $distance; right: $distance; }
  position: $type !important;
}

/* TODO Documentation */

.u-fixed--top-center {
  @mixin position top, fixed, .5em;
  width: calc(100% - 1em);
}

.u-fixed--top { @mixin position top; }
.u-fixed--top-right { @mixin position top-right; }
.u-fixed--top-left { @mixin position top-left; }
.u-fixed--bottom { @mixin position bottom; }
.u-fixed--bottom-right { @mixin position bottom-right; }
.u-fixed--bottom-left { @mixin position bottom-left; }
.u-fixed--middle { @mixin position middle; }

.u-absolute--top { @mixin position top, absolute; }
.u-absolute--top-right { @mixin position top-right, absolute; }
.u-absolute--top-left { @mixin position top-left, absolute; }
.u-absolute--bottom { @mixin position bottom, absolute; }
.u-absolute--bottom-right { @mixin position bottom-right, absolute; }
.u-absolute--bottom-left { @mixin position bottom-left, absolute; }
.u-absolute--middle { @mixin position middle, absolute; }
