/*---
 title: Menu
 section: Components
 ---

### Block

A horizontal list of block-level links using a `<ul>`.

 ```example:html
  <ul class="c-menu c-menu--block">
    <li><button class="c-menu__link">Widgets</button></li>
    <li><button class="c-menu__link">Doodads</button></li>
    <li><button class="c-menu__link">Apparatuses</button></li>
    <li><button class="c-menu__link is-active">Things</button>
      <ul class="c-menu__submenu">
        <li><button class="c-menu__link">Thingamabob</button></li>
        <li><button class="c-menu__link">Thingamajig</button></li>
        <li><button class="c-menu__link">Thinger</button></li>
      </ul>
    </li>
  </ul>
 ```

### Inline

A horizontal list of inline-level links using a `<ul>`.

 ```example:html
 <ul class="c-menu c-menu--inline">
   <li><button class="c-menu__link is-active">Link 1</button></li>
   <li><button class="c-menu__link">Link 2</button></li>
   <li><button class="c-menu__link">Link 3</button></li>
 </ul>
 ```

 */

:root {
  --menu-active-color: var(--nav-active-color);
  --menu-active-copy: var(--nav-active-copy);
  --menu-item-height: 41px;
}

.c-menu {
  @mixin list-horizontal;
  @mixin no-bullets;
  @mixin row;
  user-select: none;
}

.c-menu__link {
  background-color: transparent;
  color: currentColor;
  cursor: pointer;
  font-family: inherit;

  &:focus { outline: 0; }
}

.c-menu--block {
  background-color: transparent;
  color: currentColor;
  position: relative;
  margin: 0 -.5rem;
  text-decoration: none;
  vertical-align: middle;

  .c-menu__link {
    display: block;
    font-size: 18px;
    height: var(--menu-item-height);
    line-height: var(--menu-item-height);
    padding: 0 24px;
    transition: background-color var(--global-duration) ease, color var(--global-duration) ease;
    white-space: nowrap;
    width: 100%;

    &:hover,
    &:active,
    &.is-active {
      background-color: var(--menu-active-color);
      color: var(--menu-active-copy);
    }

    &.is-active + .c-menu__submenu {
      @mixin fade-in;
    }
  }

  .c-menu__submenu {
    @mixin fade-out;
    @mixin no-bullets;
    @mixin reset-box;
    color: currentColor;
    display: block;
    min-width: 150px;

    &.is-active {
      @mixin fade-in;
    }
  }
}

.c-menu--inline {

  .c-menu__link {
    border-bottom: 3px solid transparent;
    font-size: var(--font-size--semi-small);
    letter-spacing: 1px;
    min-height: 36px;
    padding: .5em .5rem;
    text-transform: uppercase;
    transition: border-bottom var(--global-duration) ease, color var(--global-duration) ease;

    @media (--md-up) { padding: .5em .75rem; }

    &:hover { border-bottom-color: var(--blue-400); }

    &:active,
    &.is-active {
      border-bottom-color: var(--blue-800);
      color: var(--blue-800);
    }
  }
}
