/*---
title: Card
section: Components
---*/

@define-mixin card-block $card-margin: 0 0 1em {
  margin: $card-margin;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

@define-mixin card-inline $card-margin: 0 0 1em, $card-min-width: 100px {
  @mixin card-block $card-margin;
  display: inline-block;
  max-width: 100%;
  min-width: $card-min-width;
}

@define-mixin card-solid $card-color: #f7f7f7, $card-text-color: #444, $card-shadow: 0 2px 2px rgba(0, 0, 0, .4) {
  background-color: $card-color;
  box-shadow: $card-shadow;
  color: $card-text-color;
}

@define-mixin card-hollow $card-color: #444, $card-border-width: 1px, $card-border-style: solid {
  border-color: $card-color;
  border-style: $card-border-style;
  border-width: $card-border-width;
  color: $card-color;
}

@define-mixin card $background: var(--white), $shadow: 0 0 10px rgba(0, 0, 0, .4), $padding: .5em 1rem {
  @mixin card-block;
  @mixin card-solid $background, #444, $shadow;
  padding: $padding;
}

.c-card { @mixin card; }

.c-card__heading { margin-top: 10px; }

.c-card__title {
  @mixin row;
  @mixin middle;
  background-color: var(--grey-100);
  margin: -.5rem -1rem 0;
  min-height: 38px;
  padding: 2px 10px;

  &__heading {
    display: inline-block;
    font-weight: bold;
    margin: 0;
  }
}

.c-card--row {
  @mixin middle;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .4);
  margin-bottom: 0;
  margin-left: -.5rem;
  margin-right: -.5rem;
  padding: .75em;
  position: relative;
  transition: background .1s ease-in-out, box-shadow .1s ease-in-out;

  @media (--md-up) {
    margin-left: inherit;
    margin-right: inherit;
  }

  &.is-active {
    box-shadow: 0 0 10px rgba(0, 0, 0, .4) !important;
    z-index: 2;

    .u-row {
      @mixin middle;
      &:not(:only-child) {
        margin: 0 0 .75em;
      }
      &:not(:first-child) {
        border-top: 1px solid var(--grey-100);
        margin-bottom: 0;
        padding-top: .75em;
      }
    }
  }

  &:focus .c-card__actions {
    @mixin fade-in;
  }

  @media (--md-up) {
    &:hover .c-card__actions {
      @mixin fade-in;
    }
  }
}

.c-card__actions {
  @mixin fade-toggle-child active;
  color: var(--grey-700);

  .c-icon {
    cursor: pointer;
    margin-left: .75em;
  }
}

.c-card--transparent {
  background: transparent !important;
  box-shadow: none !important;
}

.c-card--zone {
  background-color: var(--blue-50);
  border: 1px dashed var(--blue-600);
  border-radius: 4px;
  box-shadow: none;
  color: var(--blue-600);
  display: block;
  font-size: 18px;
  padding: 1em;
  text-align: center;
  text-transform: uppercase;
}