/*---
title: Field
section: Components
---*/

:root {
  --field-font-size: 14px;
  --field-min-height: 30px;
}

.c-field {
  background-color: transparent;
  border-bottom: 2px solid var(--grey-500);
  color: var(--body-copy);
  display: flex;
  font-family: inherit;
  font-size: var(--field-font-size);
  margin: 0 0 16px 0;
  min-height: var(--field-min-height); /* IE11 Input Height */
  padding: 2px 4px;
  transition: border var(--global-duration) ease-in-out;
  width: 100%;

  &:focus,
  &.has-focus {
    border-bottom-color: var(--blue-600);
    outline: none;
  }

  &:focus:invalid,
  &:focus.is-invalid { border-bottom-color: var(--orange-600); }

  &:invalid,
  &.is-invalid { border-bottom-color: var(--orange-600); }

  &:focus.is-error { border-bottom-color: var(--red-600); }

  &.is-error { border-bottom-color: var(--negative-copy); }
}

.c-field--small { font-size: var(--font-size--semi-small); }

.c-field--medium { font-size: var(--font-size--semi-medium); }

.c-field--large { font-size: var(--font-size--semi-large); }