/*---
title: Input
section: Components
---*/

:root {
  --input-focus-shadow: 0 0 6px var(--blue-600);
  --input-font-size: 14px;
  --input-min-height: 30px;
}

@define-mixin input-validation $color {
  border: 1px solid $color;
  box-shadow: 0 0 6px $color;
  outline: 0;
}

.c-input {
  display: flex;
  font-family: inherit;
  font-size: var(--input-font-size);
  margin: 0 0 16px 0;
  min-height: var(--input-min-height); /* IE11 Input Height */
  padding: 0 0 0 4px;
  width: 100%;
  background-color: var(--white);
  border: 1px solid var(--grey-600);
  border-radius: 4px;
  color: var(--body-copy);
  transition: box-shadow var(--global-duration) ease-in-out;

  &:focus,
  &.has-focus { @mixin input-validation var(--blue-600); }

  &:focus:invalid,
  &:focus.is-invalid { @mixin input-validation var(--orange-600); }

  &:invalid,
  &.is-invalid { border: var(--orange-600) 1px solid; }

  &:focus.is-error { @mixin input-validation var(--red-600); }

  &.is-error { border: var(--negative-copy) 1px solid; }

  & ~ .c-button--icon {
    padding: 0 4px;
    top: 3px;
    z-index: 10;

    .c-icon {
      color: var(--grey-600);
      height: 22px;
      width: 22px;
    }
  }
}

.c-input--round {
  border-radius: 100px;
  text-indent: 20px;
}

.c-input--multi-line {
  height: auto;
  min-height: 60px;
  outline: none;
  overflow: auto;
  padding: 8px 4px;
  resize: vertical;
}

.c-input-label {
  color: var(--black);
  cursor: pointer;
  display: block;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.5;
  margin-bottom: 4px;

  &.is-optional { font-weight: normal; }
}
