/*---
 section: Components
 title: Messages
 ---

 Use these message classes on text strings to style them for various type treatments.

 ```example:html
 <div class="c-message">This is a message</div>
 ```

 ```example:html
 <div class="c-message--alert">This is an alert message</div>
 ```

 ```example:html
 <div class="c-message--success">This is a successful message</div>
 ```

 ```example:html
 <div class="c-message--warning">This is a warning message</div>
 ```
*/

@define-mixin message $color: var(--informative-copy), $font-size: var(--font-size--semi-small) {
  color: $color;
  font-size: $font-size;
}

.c-message { @mixin message; }
.c-message--alert { @mixin message var(--negative-copy); }
.c-message--success { @mixin message var(--positive-copy); }
.c-message--warning { @mixin message var(--live-copy); }
