/*---
title: Bar
section: Objects
---*/

:root {
  --brand-logo: 'img/navis-logo--inverted.svg';
  --toolbar-height: calc(56px + 1em);
}

@define-mixin bar $background, $border-color: transparent, $border-width: 0 0 3px {
  @mixin middle;
  @mixin row;
  background-color: $background;
  color: contrast($background);
  height: 56px;
  padding: 0 .5rem;
  @media (--md-up) {
    border-color: $border-color;
    border-style: solid;
    border-width: $border-width;
  }
}

.o-bar {
  @mixin bar var(--bar-color), var(--bar-border-color), 0 0 1px;

  /* TODO Investigate */
  .c-dropdown { @mixin start; }
}

.o-bar__nav {
  @mixin column-pattern;
  padding: .5em 0;

  .o-icon-group li { margin-left: 2%; }

  &:nth-child(even) {
    @media (--md-up) {
      .o-bar__navset {
        @mixin end;
      }
      .o-icon-group li {
        margin-left: 4%;
      }
    }
  }

  &:nth-child(odd) {
    @media (--md-up) {
      .o-bar__navset { @mixin start; }
      .o-icon-group li { margin-right: 4%; }
    }
  }
}

/* TODO Find a way to get rid of this */
.o-bar__mnav {
  width: 35%;
  @media (--md-up) { width: 50%; }

  button { cursor: pointer; }
}

.o-bar__mitem button {
  padding: 1em .3rem;
  transition: background-color var(--global-duration) ease-in-out;
  @media (--md-up) { padding: 1em .5rem; }

  &:focus { outline: none; }

  @mixin active {
    background-color: var(--nav-active-color);
    color: var(--nav-active-copy);
  }
}

.o-bar__navset {
  @mixin middle;
  display: flex;
  flex-wrap: wrap;
  height: 41px;
  list-style: none;
}

.o-bar__link {
  display: inline-block;
  position: relative;
  text-transform: uppercase;
  vertical-align: middle;

  &--brand {
    /* stylelint-disable function-url-quotes */
    background-image: url(var(--brand-logo));
    /* stylelint-enable */
    background-repeat: no-repeat;
    background-size: contain;
    display: inline-block;
    height: 48px;
    margin: 6px 0 0 !important;
    position: relative;
    text-indent: -99999em;
    width: 80px;
    @media (--md-up) {
      width: 90px;
    }
  }
}

.o-bar--small {
  border-bottom-width: 3px !important;
  height: 44px;
}

.c-toolbar {
  @mixin bar var(--primary-color);
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  @media (--md-up) {
    background-color: var(--white);
    box-shadow: 0 0 3px rgba(0, 0, 0, .36);
    color: var(--body-copy);
    margin: 0 0 1em;
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    z-index: 20;
  }

  button,
  .c-button-group,
  .c-input,
  .c-subnav { margin-bottom: 0 !important; }
}

@media (--sm-down) {
  .c-toolbar__view {
    @mixin column 6;
    h1 {
      display: inline-block;
      font-size: var(--font-size--semi-medium);
      font-weight: normal;
      margin: 0;
      vertical-align: middle;
      white-space: nowrap;
    }
  }

  .c-toolbar__action {
    display: inline-block;
    padding: 1em;
    margin: 0;
  }

  .c-toolbar__actions {
    @mixin column 6;
    @mixin end;
    > div,
    > ul,
    > li {
      display: inline-block;
    }
  }
}

@media (--md-up) {
  .c-toolbar { width: 100%; }
}
