/*---
title: Typography
import: typography.md
section: Elements
---*/

:root {
  --font-family: 'Source Sans Pro', sans-serif;
  --font-size: 100%;
  --font-size--tiny: .125rem;
  --font-size--semi-tiny: .25rem;
  --font-size--small: .5rem;
  --font-size--semi-small: .75rem;
  --font-size--normal: 1rem;
  --font-size--normal-plus: 1.25rem;
  --font-size--semi-medium: 1.5rem;
  --font-size--medium: 2rem;
  --font-size--semi-large: 2.5rem;
  --font-size--large: 3rem;
  --font-line-height: 1.5;
  --font-weight: 400;
  --font-weight--bold: 800;
  --heading-font-size: 1.25rem;
}

/* TODO Get rid of this fast */

@custom-selector :--headings h1, h2, h3, h4, h5, h6;

/* Lists */

@define-mixin no-bullet {
  list-style-image: none;
  list-style-type: none;
  margin-left: 0;
}

@define-mixin no-bullets {
  list-style: none;
  &__item { @mixin no-bullet; }
}

@define-mixin list-horizontal {
  @mixin reset-box;
  clear: both;
}

@define-mixin list-item-horizontal $padding: 4px, $direction: left {
  @mixin no-bullets;
  white-space: nowrap;
  /* @mixin float $direction;  */
  padding-left: $padding;
  padding-right: $padding;
  /* todo */
}

@define-mixin list-inline $margin: 1em {
  @mixin no-bullets;
  margin-bottom: .5em;
  margin-left: .5em;
  margin-top: 0;
  overflow: hidden;
  padding: 0;

  &__item {
    display: block;
    float: left;
    margin-left: $margin;
  }
}

