/* Links */

@define-mixin focus {
  &:focus { @mixin-content; }

  @media (--lg-up) {
    &:hover { @mixin-content; }
  }
}

@define-mixin active {
  &:active,
  &.is-active { @mixin-content; }
}

@define-mixin disabled {
  &[disabled],
  &.is-disabled { @mixin-content; }
}


.c-input--collapsable {
  @mixin fade-out;

  &.is-open { @mixin fade-in; }
}

@define-mixin link-colors $normal, $hover, $active, $visited, $focus {
  color: $normal;
  &:visited { color: $visited; }
  &:focus { color: $focus; }
  &:hover { color: $hover; }
  &:active { color: $active; }
}

@define-mixin link-hover {
  text-decoration: none;

  &:hover,
  &:focus { text-decoration: underline; }
}

@define-mixin link-unstyled {
  color: inherit;
  cursor: inherit;
  text-decoration: inherit;

  &:active,
  &:focus { outline: none; }
}

/* Overflow */

@define-mixin ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

@define-mixin break-word {
  /* Safari can wait */
  -ms-word-break: break-all;
  word-break: break-word;
  word-wrap: break-word;
}

/* Typography classes */

.u-no-bullet { @mixin no-bullet; }
.u-no-bullets { @mixin no-bullets; }
.u-list-horizontal { @mixin list-horizontal; }
.u-ellipsis { @mixin ellipsis; }
.u-break-word { @mixin break-word; }

/* TODO Move to objects/components layer.
   Custom feature object for intro copy */
.o-feature { font-size: var(--font-size--semi-medium); }

::selection {
  background-color: var(--highlight-warning);
  color: var(--body-copy);
  text-shadow: none;
}

:root {
  /* stylelint-disable root-no-standard-properties */
  color: var(--body-copy);
  font: var(--font-size) / var(--font-line-height) var(--font-family);
  font-weight: var(--font-weight);
  line-height: 1;
  text-rendering: optimizeLegibility;
  text-size-adjust: 100%; /* iOS 8+ */
  /* stylelint-enable */
}

:--headings {
  font-size: var(--heading-font-size);
  font-weight: inherit;
  line-height: 1.3;
  margin: 0 0 8px;
}

/* specify the font family of code elements */
code,
kbd,
pre,
samp { font-family: monospace, monospace; }

/* specify the list style of nav lists */

hr {
  border-top: 2px solid var(--grey-300);
  margin: 1em 0;
}

a[href]:not([class]) {
  @mixin link-hover;
  color: var(--link-color);
  text-decoration: none;
}

p {
  line-height: 1.4;
  margin-bottom: 1em;
}

small {
  display: block;
  font-size: 75%;
  line-height: 1.5;
  margin-bottom: 1em;
}

figure {
  display: inline-block;
  float: left;
  margin: 0 1rem 1em 0;
  overflow: hidden;
  text-align: center;
}

figcaption {
  font-size: var(--font-size--semi-small);
  padding: .5em 0;
}
