@import 'stylep-button';

/*---
 section: Objects
 title: Button Groups
 ---
 #### Horizontal

 ```example:html
 <div class='o-button-group'>
   <button class='c-button'>Play</button>
   <button class='c-button'>Pause</button>
   <button class='c-button'>Stop</button>
 </div>
 ```

 #### Vertical

 ```example:html
 <div class='o-button-group--vertical'>
   <button class='c-button'>Play</button>
   <button class='c-button'>Pause</button>
   <button class='c-button'>Stop</button>
 </div>
 ```
*/

@define-mixin button-group-vertical {
  clear: both;
  list-style: none;

  .c-button {
    margin: 0 0 2px 0 !important;
    width: 100%;
  }
}

.o-button-group {
  @mixin button-group-inline;
  @mixin button-group-pill;
  display: inline-block;
}

.o-button-group--vertical { @mixin button-group-vertical }

.o-button-group__label {
  display: inline-block;
  margin: 0 .5rem 0 !important;
  line-height: 30px !important;
}

.o-button-group--toggle {
  @mixin button-group;
  @mixin text-middle;
  border: 1px solid currentColor;
  border-radius: var(--global-rounded);
  display: inline-flex;
  height: 30px;
  line-height: 30px;
  margin-bottom: .5em;
  margin-right: 2%;
  overflow: hidden;

  @media (--md-up) { margin-bottom: 0; }
  
  .c-button {
    border: none;
    height: 26px;
    line-height: 26px;
    margin: 1px !important;
    min-width: 50px;
    transition: color var(--global-duration) ease, background-color var(--global-duration) ease !important;

    &:hover,
    &:focus { border: none; }

    &:not(.is-active):hover,
    &:not(.is-active):focus {
      /* Dashboard stuff */
      /* background-color: transparent;
      color: var(--white); */
      padding: inherit;
    }

    &::after {
      box-shadow: none;
    }
  }

  .is-active {
    /* Dashboard stuff */
    /* background-color: var(--white);
    color: var(--primary-color); */
  }

  .c-toolbar & { margin-bottom: 0 !important; }
}

.o-button-group--postfix {
  .c-button--postfix {
    border-radius: 0;
    &:last-child { border-radius: 0 var(--global-rounded) var(--global-rounded) 0; }
  }
}
