/* ---
 section: Components
 title: Badges
 ---

 ```example:html
 <p><a href="#">Inbox <span class="c-badge">7</span></a></p>

 <p><button class="c-button">
   Messages <span class="c-badge">37</span>
 </button></p>
 ```

*/

@define-mixin badge-inline
$badge-font-size: 75%,
$badge-padding: 3px 8px {
  display: inline-block;
  font-size: $badge-font-size;
  line-height: 1;
  max-width: 100%;
  outline: 0;
  padding: $badge-padding;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;

  &:empty { display: none; }
}

@define-mixin badge-solid
$badge-color: #555,
$badge-text-color: #fff,
$badge-radius: 50px {
  background: $badge-color;
  border-radius: $badge-radius;
  color: $badge-text-color;
}

@define-mixin badge-hollow
$badge-color: #555,
$badge-radius: 50px,
$badge-border-style: solid,
$badge-border-width: 1px {
  border-color: $badge-color;
  border-radius: $badge-radius;
  border-style: $badge-border-style;
  border-width: $badge-border-width;
  color: $badge-color;
}

.c-badge {
  @mixin badge-inline;
  @mixin badge-solid var(--grey-600);

  .c-button & {
    @mixin badge-solid var(--white), var(--blue-800);
    margin-bottom: 3px;
  }

  .c-button--success & { @mixin badge-solid var(--white), var(--green-700); }
  .c-button--secondary & { @mixin badge-solid var(--white), var(--orange-700); }
  .c-button--alert & { @mixin badge-solid var(--white), var(--red-700); }
}

.c-badge--default { @mixin badge-solid transparent var(--body-copy); }
.c-badge--positive { @mixin badge-solid var(--green-600); }
.c-badge--informative { @mixin badge-solid var(--blue-600); }
.c-badge--live { @mixin badge-solid var(--gold-700); }
.c-badge--alterable { @mixin badge-solid var(--orange-600); }
.c-badge--negative { @mixin badge-solid var(--red-600); }
.c-badge--purple { @mixin badge-solid var(--magenta-500); }

