/*---
title: Modal
section: Objects
---*/

@define-mixin modal $titlebar: var(--primary-color), $background: var(--grey-100) {
  @mixin fade-out;
  @mixin row;
  @mixin start;
  position: fixed;
  z-index: 500;

  @media (--md-up) {
    position: static;
  }

  &.is-active {
    @mixin fade-in;
    z-index: 300; /* Move on-top of dropdowns */
  }

  &__backdrop {
    @mixin fade-out;
    background: rgba(0, 0, 0, .6);
    bottom: 0;
    height: 100%;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 400 !important;

    @media (--md-up) {
      .is-active & { @mixin fade-in; }
    }
  }

  &__card {
    @mixin card $background;
    min-height: 120px;
    min-width: 370px;
    padding: 0;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 56px;
    left: 0;
    z-index: 450;
    @media (--sm-down) {
      box-shadow: none;
      margin-bottom: 0;
    }
    @media (--md-up) {
      @mixin position middle;
    }
  }

  &__title {
    background-color: $titlebar;
    color: var(--white);
    font-size: var(--font-size--semi-medium);
    margin-bottom: .5em;
    margin-top: -1px;
    padding: .5em 1rem;
    position: relative;

  }

  &__close {
    background-color: transparent;
    cursor: pointer;
    display: none;
    position: absolute;
    right: 5px;
    top: 5px;
    @media (--md-up) {
      display: inline;
    }

    .c-icon {
      color: rgba(255, 255, 255, .5);
      height: 40px;
      width: 40px;
    }
  }

  &__back {
    background-color: transparent;
    cursor: pointer;
    display: inline;

    @media (--md-up) {
      display: none;
    }

    .c-icon {
      color: rgba(255, 255, 255, .5);
      height: 40px;
      width: 40px;
    }
  }

  &__content {
    padding: .5em 1rem;
    > div:not([class]) { @mixin column 12; }
  }

  &__actions {
    @mixin column 12;
    @mixin end;
  }
}

.o-modal {
  @mixin modal;

  &--help {
    @mixin modal var(--blue-700), var(--white);

    &__title {
      @mixin row;
      @mixin middle;
      border-radius: 4px 4px 0 0;

      .c-icon:first-child { margin-right: 5px; }

      .o-modal__close .c-icon {
        height: 30px !important;
        top: .4rem;
        width: 30px !important;
      }

      h6 {
        display: inline-block;
        margin: 5px 0 !important;
      }
    }

    &__card { border-radius: 4px; }
  }
}

.o-modal--success,
.o-modal--primary {
  .o-modal__card {
    padding: .5em 1rem 1em;
    color: var(--white);
  }
}

.o-modal--success {
  .o-modal__card { background-color: var(--green-800); }
}

.o-modal--primary {
  .o-modal__card { background-color: var(--blue-800); }
}

.o-modal--warning {
  .o-modal__card {
    background-color: var(--gold-700);
    padding: .5em 1rem 1em;
  }
}
