/*---
title: Layout
import: layout.md
section: Elements
---*/

@define-mixin flex-layout {
  display: flex;
  flex-direction: column;
}

@define-mixin reset-all-the-things {
  border-style: solid;
  border-width: 0;
  box-sizing: inherit;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

@define-mixin app-leader {
  height: calc(100vh - var(--mobile-header-height));
  overflow-x: hidden;
  @media (--md-up) {
    height: calc(100vh - var(--header-height));
  }
}

/* Global Styles */

::before,
::after {
  @mixin reset-all-the-things;
  text-decoration: inherit;
  vertical-align: inherit;
}

* {
  @mixin reset-all-the-things;
  background-repeat: no-repeat;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --header-height: 56px;
  --mobile-header-height: var(--header-height);
  --sidenav-width: 12.25em;
  /* stylelint-disable root-no-standard-properties */
  background-color: var(--background-color);
  box-sizing: border-box;
  cursor: default;
  margin: 0;
  -ms-overflow-style: -ms-autohiding-scrollbar; /* Edge 12+, Internet Explorer 11- */
  overflow-x: hidden;
  overflow-y: scroll; /* All browsers without overlaying scrollbars */
  width: 100%;
  /* stylelint-enable */
}

/* Application Scope */

.s-app {
  @mixin app-leader;
}

.s-app__container {
  @mixin app-leader;
  overflow-x: hidden;
}

.s-app__body {
  @mixin app-leader;
  @mixin flex-layout;
  margin-top: var(--mobile-header-height);
  @media (--md-up) {
    margin-top: var(--header-height); /* combined height of .o-header */
  }
}

.o-header {
  @mixin position top;
  z-index: 200;
}

.o-main {
  @mixin container-fluid;
  @mixin row;
  @mixin row-break;
  flex-direction: column;
  position: relative;
  @media (--md-up) {
    flex-direction: row;
  }
}

.o-nav {
  @mixin reset-mobile-order;
  background-color: var(--sidenav-color);
  box-shadow: var(--sidenav-shadow);
  flex: 1 6 20%;
  order: 1;
  overflow-x: hidden;
  overflow-y: scroll;
  padding-right: 0;
  @media (--md-up) {
    height: 100%;
    position: fixed;
    width: var(--sidenav-width);
    z-index: 10;
  }

  & .c-button,
  & .c-input {
    margin-bottom: 0 !important;
  }
}

.o-article {
  @mixin reset-mobile-order;
  flex: 2 1 100%;
  order: 2;
  position: relative;
  .o-nav + & {
    flex: 2 1 80%;
    @media (--md-up) { margin-left: var(--sidenav-width); }
  }
}
