title: Images
import: images.md
section: Elements
---*/

@define-mixin triangle $direction, $color: currentColor, $size: 1em {
  @if $direction == right { border-left: calc($size * 1.5) solid $color; }
  @if $direction == top { border-bottom: calc($size * 1.5) solid $color; }
  @if $direction == bottom { border-top: calc($size * 1.5) solid $color; }
  @if $direction == left { border-right: calc($size * 1.5) solid $color; }
  @if $direction == top or $direction == bottom {
    border-left: $size solid transparent;
    border-right: $size solid transparent;
  } @else if $direction == right or $direction == left {
    border-bottom: $size solid transparent;
    border-top: $size solid transparent;
  }
  content: '';
  display: inline-block;
  height: 0;
  width: 0;
  z-index: 2;
}

@define-mixin circle $bgColor: var(--neutral-color), $color: var(--white), $padding: 6px {
  background-color: $bgColor;
  border-radius: 50px;
  color: $color;
  padding: $padding;
  .c-icon {
    height: 16px;
    width: 16px;
  }
}

@define-mixin checkmark $color: var(--body-copy), $height: 11px, $width: 6px, $thickness: 2px {
  border: solid $color;
  border-width: 0 $thickness $thickness 0;
  display: block;
  height: $height;
  transform: rotate(45deg);
  width: $width;
}

img { width: 100%; }

/* TODO Either move these into the object/component layer or find a state equivalent */
.o-circle { @mixin circle var(--neutral-copy); }
.o-circle--blue { @mixin circle var(--blue-600); }
.o-circle--red { @mixin circle var(--negative-copy); }
.o-circle--green { @mixin circle var(--positive-copy); }
.o-circle--yellow { @mixin circle var(--gold-500); }

.o-image--small {
  max-height: 200px;
  max-width: 200px;
}

/* Insight Specific */
.o-facebook-logo,
.o-twitter-logo {
  path {
    fill: var(--grey-700);
  }
}
