/*---
title: Notification
section: Components
---*/

:root {
  --notification-margin: calc(var(--global-outter-margin) - .5rem);
  --notification-width: 400px;
  --notification-color: blue;
  --notification-success-color: green;
  --notification-warning-color: gold;
  --notification-error-color: gold;
}

@define-mixin notification-fixed {
  margin: 0 auto !important;
  position: fixed !important;
  z-index: 200 !important;
}

.c-notification {
  @mixin card var(--white), 0 0 10px rgba(0, 0, 0, .4), 12px;
  padding: .5rem !important;
  position: relative;
  text-align: left;
  transition-duration: var(--global-duration);
  @media (--md-up) { width: var(--notification-width); }

  & > .c-icon {
    color: var(--blue-700);
    left: 16px;
    position: absolute;
    top: 12px;
    @media (--md-up) {
      font-size: 1.5rem;
      margin-top: .25rem;
    }
  }

  .c-button--icon {
    padding: 0;
    position: absolute;
    right: 12px;
    top: 12px;
  }

  &.u-fixed--top { border-radius: 0 0 var(--global-rounded) var(--global-rounded); }
  &.u-fixed--bottom { border-radius: var(--global-rounded) var(--global-rounded) 0 0; }
}

.c-notification__heading {
  font-size: 14px;
  font-weight: bold;
  line-height: 1.3;
  margin: 7px 0 0;
}

.c-notification__content {
  display: inline-block;
  font-size: 14px;
  left: 40px;
  position: relative;
  width: 87%
}

/* States */

.c-notification--success {
  @mixin card;
  & > .c-icon { color: var(--green-700); }
}

.c-notification--warning {
  @mixin card var(--gold-50);
  & > .c-icon { color: var(--orange-600); }
}

.c-notification--error {
  @mixin card var(--gold-50);
  & > .c-icon { color: var(--red-700); }
}
