.o-inspector {
  @mixin card-block;
  @mixin card-solid var(--white), #444, none;
  border: 1px solid rgba(255, 255, 255, 0);
  transition: border .1s ease;

  &:focus,
  &:hover,
  &.is-focused {
    border: 1px solid var(--blue-200);

    & .o-inspector__actions { @mixin fade-in; }
  }

  &.is-active {
    border-left: 1px solid var(--blue-200);
    border-right: 1px solid var(--blue-200);

    & .o-inspector__toolbar { @mixin fade-in; }

    & .o-inspector__actions { @mixin fade-out; }
  }
}

.o-inspector__toolbar {
  @mixin fade-out;
  @mixin row;
  @mixin row-collapse;
  background-color: var(--blue-200);
  height: 38px;

  .c-menu__link {
    font-size: 14px;
    height: 38px;
    line-height: 38px;
    min-width: 100px;

    &.is-active {
      background-color: var(--blue-800);
      box-shadow: inset 0 0 6px rgba(0, 0, 0, .8);
    }
  }

  select {
    display: inline-block;
    font-size: 13px;
    height: 30px;
    margin: 0 0 3px;
    max-width: 50%;
  }

  label {
    display: inline-block;
    margin: 5px 10px 0 0;
  }

  .c-badge {
    border-radius: 50%;
    font-size: 16px;
    font-weight: bold;
    height: 25px;
    line-height: 1.25;
    margin-left: .5em;
    margin-right: .5em;
    padding: 2px 0;
    width: 25px;

    .c-icon {
      height: 18px;
      margin-top: -4px;
      width: 18px;
    }
  }

  &--small {
    height: 24px;

    .c-button {
      height: 25px;
      line-height: 25px;
      margin-left: 1.5px;
      margin-right: 1.5px;
      min-height: 25px;
      padding: 1px 0 0;
    }

    .c-icon {
      margin-bottom: 3px;
    }
  }
}

.o-inspector__actions {
  @mixin fade-out;
  position: absolute;
  right: 0;
  top: 0;
}

.o-inspector__more-action {
  margin-right: .5em;
}

.o-inspector__content {
  @mixin start;
  padding: .5em 1rem;
}


.c-button--favorite {
  @mixin button-solid transparent, transparent, var(--blue-700), none, var(--button-round);
  font-size: 14px !important;
  margin: 0 !important;
  padding: 8px 0 !important;
  position: relative;
  text-transform: uppercase;

  .c-icon {
    bottom: 1.5px;
    color: var(--blue-700);
    height: 23px;
    position: relative;
    transition: color .1s ease-in-out;
    width: 23px;
  }

  &::before {
    background-color: transparent;
    border-radius: 50%;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    content: '';
    display: inline-block;
    height: 8px;
    left: 8px;
    position: absolute;
    top: 16px;
    transition: box-shadow .1s ease;
    width: 8px;
  }

  &.is-active {
    background-color: transparent;
    box-shadow: none;

    &::before {
      box-shadow: 0 0 10px rgba(0, 0, 0, .72), 0 0 10px rgba(0, 0, 0, .72);
    }

    .c-icon { color: var(--gold-200); }
  }
}


