/*---
title: Dropdowns
section: Components
---*/

@import 'stylep-dropdown';

:root {
  --toggle-height: 30px;
  --toggle-width: 125px;
}

@define-mixin toggle {
  background-color: var(--white);
  border: 1px solid var(--toggle-border-color);
  border-radius: var(--global-rounded);
  color: var(--body-copy);
  cursor: pointer;
  display: inline-block;
  height: var(--toggle-height);
  position: relative;
  text-align: left;
}

select {
  @mixin toggle;
  font-family: inherit;
  font-size: 18px;
  font-weight: normal !important;
  margin: 0 0 16px 0;
  width: 100%;

  &::-ms-expand {
    display: none !important; /* Edge 12+, Internet Explorer 11- */
  }

  &::-ms-value {
    color: currentColor; /* Edge 12+, Internet Explorer 11- */
  }
}

.c-dropdown {
  cursor: pointer;
  display: inline-block;
  height: auto;
  position: relative;
  z-index: 20;

  &:focus { outline: 0; }

  & .c-button--default {
    margin-bottom: 0;
    min-height: 34px;
    padding: 4px 8px;
    text-align: left;
    width: 100%;
  }
}

.c-dropdown__toggle {
  @mixin toggle;
  min-width: var(--toggle-width);
  padding: 0 .5rem;
  text-decoration: none;

  &:focus { outline: 0; }

  &::before {
    @mixin triangle 'bottom', var(--body-copy), 5px;
    position: absolute;
    right: .5rem;
    top: calc(var(--toggle-height) / 2 - 3px); /* Half the toggle height minus half the triangle height */
  }
}

.c-dropdown__toggle--large {
  height: 42px;
}

.c-dropdown__text-toggle {
  border: 2px solid transparent;
  border-radius: var(--global-rounded);
  color: var(--white);
  cursor: pointer;
  font-size: 1.125em;
  font-weight: 400;
  margin: 0;
  padding: 5px 5px 5px 0;
  text-transform: uppercase;
  @media (--md-up) {
    margin: 0 0 0 13px;
    padding: 5px 13px 5px 0;
  }

  &:hover,
  &:focus,
  &.is-active {
    background-color: var(--nav-active-color);
    color: var(--nav-active-copy);

    &::before { border-left-color: var(--nav-active-copy); }
    .c-icon { color: var(--nav-active-copy); }
  }

  &.is-active::before { transform: rotate(90deg); }

  &::before {
    @mixin triangle right, rgba(255,255,255,.5), 4px;
    margin: -2px 8px 0 14px;
    transition-duration: .3s;
  }
}

.c-dropdown__content {
  @mixin dropdown-solid var(--callout-bg-color), var(--grey-100), rgba(0, 0, 0, .75), 4px, var(--toggle-border-color) 0 0 7px;
  @mixin card-inline 3px, 0;
  @mixin fade-out;
  list-style: none;
  min-width: 160px;
  padding: 0;
  position: absolute;
  left: 0;
  top: 35px;

  &.is-active { @mixin fade-in; }

  &::before { right: 20px !important; }

  &::after,
  &:not(.is-active) svg { display: none; }

  .c-icon {
    color: #444;
    height: 16px;
    top: 3px !important;
    width: 16px;
  }

  &--overlap {
    left: 13px !important;
    top: -3px !important;
    width: 165px !important;
  }
}

.c-dropdown__icon {
  color: var(--primary-color);
  height: 24px;
  margin-right: 8px;
  width: 24px;
}

.c-dropdown__link {
  background-color: transparent;
  cursor: pointer;
  font-family: inherit;
  font-size: 14px;
  padding: 14px 18px;
  text-align: left;
  text-transform: uppercase;
  transition: background-color var(--global-duration) ease, color var(--global-duration) ease;
  width: 100%;

  &:focus { outline: 0; }

  &:hover,
  &:focus { background-color: var(--blue-100); }

  &[disabled],
  &.is-disabled {
    box-shadow: none;
    opacity: .5;
  }

  &:active,
  &.is-active {
    background-color: var(--primary-color);
    color: var(--white);
  }
}

.c-dropdown__group {
  border-bottom: 1px solid var(--grey-500);
}

.c-dropdown--right .c-dropdown__content {
  left: auto;
  right: 0;
}

.c-dropdown--picker {
  .c-dropdown__content {
    min-width: 13.5em;
    padding: var(--frame-third);
  }
}

.c-dropdown__picker {
  box-shadow: inset 0 10px 11px -7px rgba(0, 0, 0, .5), inset 0 -10px 11px -7px rgba(0, 0, 0, .5);
  height: 178px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 0 1rem;
  margin-bottom: var(--frame-quarter);
}
